import DatabaseCard from './cardTypes/DatabaseCard';
import DocumentCard from './cardTypes/DocumentCard';
import MessageCard from './cardTypes/MessageCard';
import ServerCard from './cardTypes/ServerCard';

function Card(item) {
    function checker(){
        return item.theme === 'light' ? 'dark' : 'light'
    }
    return (
        <div className={manager('styleType', item)}>
            <div className={'text-'+checker()} >
                {manager('image', item)}
                <p style={{ maxWidth: '10rem' }} className='text-center mx-auto' >{item.text}</p>
            </div>
        </div>
    )
}

const manager = (elementType,  item) => {
    if (elementType == 'image') {
        return item.svg == 'message' ? <MessageCard /> : item.svg == 'document' ? <DocumentCard /> : item.svg == 'database' ? <DatabaseCard /> : <ServerCard />
    } else if (elementType == 'styleType') {
        return item.cardType == 'active' ? Active() : Disable()
    }
}

const Active = () => {
    return 'col-6 mx-auto col-lg mx-lg-3 my-4 my-lg-0 text-center border-bottom border-primary border-2'
}

const Disable = () => {
    return 'col-6 mx-auto col-lg mx-lg-3 my-4 my-lg-0 text-center border-bottom border-2'
}

export default Card