import NavbarBrand from './NavbarBrand'
import NavbarLinks from './NavbarLinks';

function Navbar(props) {
    let theme = props.theme
    return (
        <div className='container'>
            <nav className={'navbar navbar-expand-lg navbar-'+theme+' bg-'+theme+' px-5'}>
                <div className="container-fluid px-5">
                    <NavbarBrand theme={props.theme} />
                    <NavbarLinks theme={props.theme} action={props.action} />
                </div>
            </nav>
        </div>
    )
}
// className="navbar navbar-expand-lg navbar-dark bg-dark px-5"
export default Navbar