import JumboButton from './JumboButton'

function Jumbotron(props) {
    let theme = props.theme
    function checker(){
        return theme === 'light' ? 'dark' : 'light'
    }
    return (
        <div className='jumbotron p-5' style={{ height: '30rem' }} >
            <div className="container">
                <div className="row" style={{ height: '30rem' }}>
                    <div className={"col-lg-7 col-12 text-"+checker()+" mx-auto text-center my-auto "}>
                        <span className='Big-font dt' >How it works</span>
                        <p className={'text-'+checker()+'  mx-auto'} style={{maxWidth:'32rem'}} >No phone calls. No faxes. No navigation patient portals. PicnicHealth gathers all of your medical records so you don't have to</p>
                        <JumboButton/>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Jumbotron