function ContactType(props) {
    let type = props.type
    return type === 'input' ? <ContactInput text={props.text} /> : <ContactTextarea text={props.text} />
}

function ContactInput(props) {
    let text = props.text
    return (
        <div className="mt-3">
            <p className='m-0' >{text}</p>
            <input type="text" className='form-control' />
        </div>
    )
}

function ContactTextarea(props) {
    let text = props.text
    return (
        <div className="mt-3">
            <p className='m-0'>{text}</p>
            <textarea name="" className='w-100 form-control' id="" rows="3"></textarea>
        </div>
    )
}

export default ContactType