import '../CustomStyle.css'
import NavbarSMButton from './NavbarSMButton'
import { Link } from "react-router-dom";

function NavbarLinks(props) {
    let theme = props.theme
    function checker(){
        theme === 'light' ? bodyLight() : bodyDark()
        return theme === 'light' ? 'light' : 'dark'
    }
    return (
        <div className='ml-auto' >
            <NavbarSMButton/>
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                    <a onClick={props.action} className={'btn btn-'+checker()+' text-center d-none d-xl-inline'} >Change Theme</a>
                    <Link className='nav-link active mx-1' to='/about'>
                        About Us
                    </Link>
                    <Link className="nav-link mx-1 active" to='/contacts' >
                        Contact Us
                    </Link>
                    <a className="nav-link mx-1 border-bottom border-primary" href="#">How it works</a>
                    <a className="nav-link mx-1" href="#">Pricing</a>
                    <a className="nav-link py-1 d-flex Button-custom my-auto btn btn-secondary text-light mx-1" href="#">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-plus-circle my-auto text-light" viewBox="0 0 16 16">
                            <path fillRule="evenodd" d="M8 15A7 7 0 1 0 8 1a7 7 0 0 0 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z" />
                            <path fillRule="evenodd" d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z" />
                        </svg>
                        <p className='my-auto' >LOGIN</p>
                    </a>
                </div>
            </div>
        </div>
    )
}

function bodyDark(){
    let body = document.getElementsByTagName('body')[0].style
    body.backgroundColor='#212529'
    body.webkitTapHighlightColor='transparent'
    body.color = 'rgb(205, 201, 195);'
}
function bodyLight(){
    let body = document.getElementsByTagName('body')[0].style
    body.backgroundColor='white'
    body.webkitTapHighlightColor='transparent'
    body.color = 'black;'
}

// 'btn btn-light text-center d-none d-xl-inline'
export default NavbarLinks