function FooterLink(item){
    item.title ? null : console.error(item + "dont have title, please give it!")
    let theme = item.theme
    function checker(){
        return theme === 'light' ? 'Footer-link-dark' : 'Footer-link-light'
    }
    return (
        <div className="col-6 col-lg">
            <h5>{item.title}</h5>
            <ul className='p-0 No-style' >
                <li> <a className={checker()} href="#">{item.text1}</a> </li>
                <li> <a className={checker()} href='#' >{item.text2}</a></li>
                <li> <a className={checker()} href="#"> {item.text3}</a></li>
            </ul>
        </div>
    )
}

export default FooterLink