import ContactType from './ContactType'
import Button from './Button';
import ContactHead from './ContactHead';

function Contact(props) {
    let theme = props.theme
    function checker(){
        return theme === 'light' ? 'dark' : 'light'
    }
    return(
        <div className='jumbotron px-3 py-5 my-5' >
            <div className="container">
                <div className="row">
                    <div className={"col-9 text-"+checker()+" bg-"+theme+" col-sm-8 col-md-7 col-lg-6 col-xl-5 py-4 px-3 mx-auto rounded border border-"+checker()}>
                        <div className="row">
                            <ContactHead text='FIRST NAME' />
                            <ContactHead text='LAST NAME' />
                        </div>
                        <ContactType text='EMAIL' type='input'/>
                        <ContactType text='MESSAGE' type='textarea'/>
                        <ContactType text='ADDITIONAL DETAILS' type='textarea' />
                        <Button/>
                    </div>
                </div>
            </div>
        </div>
    )
}
 
export default Contact